/*======== loop 라이브러리 css ========*/
:root {
  --gap-width: 20px;
  --slide-width: 210px;
  --slide-height: 210px;
  --slide-bg-color: #a7a7a763;
  --overlay-bg-color: rgba(0, 0, 0, 0.5);
  --overlay-text-color: #fff;
  --overlay-transition: opacity 0.3s ease;
  --border-radius: 20px;}

.carousel {overflow: hidden; width: 100%;}
.carousel-track {display: flex;gap: var(--gap-width);animation: scroll var(--scroll-duration) linear infinite;transform: translateX(calc(-50% + var(--slide-width) / 2));}
.slide {display: flex;justify-content: center;align-items: center;background: var(--slide-bg-color);flex: 0 0 var(--slide-width);height: var(--slide-height);border-radius: var(--border-radius);position: relative;overflow: hidden;}
.slide img {width: 70%;height: 70%;object-fit: contain;transform: none; /* 기존 transform 제거 */}
/* .slide {background: var(--slide-bg-color);flex: 0 0 var(--slide-width);height: var(--slide-height);border-radius: var(--border-radius);position: relative;overflow: hidden;}
.slide img {width: 70%;height: 70%;object-fit: cover;transform: translate(-12%, -12%);} */
/* .slide:hover {transform:  scale(1.3);-webkit-transform: scale(1.3);-ms-transform: scale(1.3);transition-duration: 1s;} */
.overlay {position: absolute;bottom: 0;background: var(--overlay-bg-color);color: var(--overlay-text-color);width: 100%;height: 100%;opacity: 0;transition: var(--overlay-transition);border-radius: var(--border-radius);display: flex;justify-content: center;align-items: center;}
/* .slide:hover .overlay {opacity: 1;} */

@keyframes scroll {0% {  transform: translateX(0);}
  100% {  transform: translateX(calc(-1 * var(--total-width)));}
}

/* .carousel-track:hover {animation-play-state: paused;} */

/* ======== 반응형: 모바일 전용 (최대 767px) ======== */

@media screen and (max-width: 375px) {
  :root {
  --gap-width: 20px;
  --slide-width: 110px;
  --slide-height: 110px;
  --slide-bg-color: #a7a7a763;
  --overlay-bg-color: rgba(0, 0, 0, 0.5);
  --overlay-text-color: #fff;
  --overlay-transition: opacity 0.3s ease;
  --border-radius: 20px;}
.slide img {width: 60%;height: 60%;object-fit: contain;transform: none; /* 기존 transform 제거 */}
.overlay {position: absolute;bottom: 0;background: var(--overlay-bg-color);color: var(--overlay-text-color);width: 40%;height: 40%;opacity: 0;transition: var(--overlay-transition);border-radius: var(--border-radius);display: flex;justify-content: center;align-items: center;}
.header {
    flex-direction: row; /* 컬럼에서 로우로 변경하여 가로 정렬 */
    align-items: center; /* 세로축 중앙 정렬 (로고와 내비게이션 높이 다를 때 유용) */
    justify-content: space-between; /* 로고와 네비게이션을 양쪽 끝으로 정렬 */
    height: 80px;
    padding-top: 8px 20px;
    width: 100%; /* 부모에 꽉 차도록 */
    box-sizing: border-box; /* padding 포함 너비 계산 */
}
.header .logo {
    width: 50%; /* 고정된 너비 대신 내용에 따라 자동 조정 (또는 적절한 픽셀/vw 값) */
    padding-top: 0; /* padding-top 제거 */
    max-width: 120px;
}

/* 네비게이션 메뉴 (nav ul) 에도 flex 속성 추가 */
nav ul {
    display: flex; /* li 요소들을 가로로 나열 */
    gap: 10px; /* li 요소들 사이의 간격 */
    list-style: none; /* 기본 리스트 스타일 제거 */
    padding: 0; /* 기본 패딩 제거 */
    margin: 0; /* 기본 마진 제거 */
}

nav ul li a {
    font-size: 10px;
    white-space: nowrap; /* 텍스트가 줄바꿈되지 않도록 */
}
