/* sub.css */

/*============== work 페이지 메인 (Project List) ==============*/

.SubWrap {
    padding-top: 60px; /* Header 높이와 맞추어 모바일에서 조정 */
}
.SubWrap .sub {}
.SubWrap .sub .item {} /* 이 선택자는 너무 일반적이므로, 실제 컴포넌트 클래스에 따라 구체화 필요 */

.SubWrap .sub .item .listHeader {
    padding: 0 5vw; /* 좌우 패딩을 vw로 설정 */
    padding-top: 5vh; /* 상단 패딩을 vh로 설정, 스크롤 영역 고려 */
}
.SubWrap .sub .item .listHeader h4 {
    font-size: 1.5rem; /* 모바일에서 1.8em 대신 rem 사용 (base font 16px 기준 24px) */
    font-weight: 600; 
    color: #000; 
    /* width: calc(33.3333333333% - 0.6666666667 * 0.5em); -> 이 복잡한 calc 대신 flex/grid로 제어 */
    width: 100%; /* 모바일에서는 100% 너비 */
    border-top: solid #D9D9D9 2px;
}
.SubWrap .sub .item .listHeader ul {
    display: flex; 
    flex-wrap: wrap; /* li가 다음 줄로 넘어가도록 설정 */
    gap: 0.8rem; /* 0.5em 대신 rem 사용 */
    padding-top: 2rem; /* 40px 대신 rem 사용 */
    justify-content: center; /* 가운데 정렬 */
}
.SubWrap .sub .item .listHeader ul li {
    /* 모바일에서는 1열 또는 2열로, 너비를 유동적으로 설정 */
    width: 100%; /* 기본: 모바일에서는 100% (1열) */
    max-width: 350px; /* 너무 커지지 않도록 최대 너비 설정 (옵션) */
    /* width: calc(50% - 0.25em); 2열로 하고 싶다면 이렇게 설정 */
    
    filter: grayscale(1); 
    transition: all 0.3s; 
    padding-bottom: 1.25rem; /* 20px 대신 rem 사용 */
    overflow: hidden; /* 이미지 오버플로우 방지 */
    border-radius: 8px; /* 카드형 디자인 시 활용 */
}
.SubWrap .sub .item .listHeader ul li:hover {filter: grayscale(0);}
.SubWrap .sub .item .listHeader ul li img {
    width: 100%; /* 이미지가 li의 너비를 꽉 채우도록 */
    height: auto; /* 비율 유지 */
    display: block;
}

/* .SubWrap .sub .item02 .listHeader ul li {display: flex; flex-wrap: wrap; gap: 0.5em;} -> item에 대한 구체적인 스타일은 아래 미디어쿼리에서 조정 */
/* .SubWrap .sub .item03 .listHeader ul li { ... width: 370px; height: 420px; } -> 고정 width/height 제거 */
/* item03 특정 li에 대한 고정 크기 제거, 반응형으로 변경 */
.SubWrap .sub .item03 .listHeader ul li {
    width: 100%; /* 모바일에서는 100% */
    height: auto; /* 고정 높이 제거 */
    max-width: 350px; /* 옵션 */
    filter: grayscale(1); 
    transition: all 0.3s;  
}


/*============== work 페이지 상세 (Project Detail) ==============*/

.DetailWrap {
    padding-top: 60px; /* Header 높이와 맞추어 모바일에서 조정 */
}
.DetailWrap .intro {
    padding: 0 5vw; /* 좌우 패딩을 vw로 설정 */
    padding-top: 5vh; /* 상단 패딩을 vh로 설정 */
    display: flex; 
    justify-content: center; 
    flex-direction: column; 
    align-items: center; 
    gap: 0.8rem; /* rem 사용 */
    text-align: center; /* 텍스트 중앙 정렬 */
}
.DetailWrap .intro div {font-size: 1.2rem; font-weight: 600;} /* rem 사용 */
.DetailWrap .intro h2 {
    padding-bottom: 1rem; /* rem 사용 */
    font-size: 2rem; /* rem 사용 */
    font-weight: 800; 
    color: #000;
}
.DetailWrap .intro hr {display: block; border-top: 2px solid #D9D9D9; width: 100%; height: 1px;}

.DetailWrap .detail {
    padding: 0 5vw; /* 좌우 패딩을 vw로 설정 */
    padding-top: 5vh; /* 상단 패딩을 vh로 설정 */
    gap: 1.5rem; /* 24px 대신 rem 사용 */
    display: flex; 
    flex-direction: column; /* 모바일에서는 세로 정렬 */
    justify-content: center; 
    align-items: center; /* 가운데 정렬 */
}
.DetailWrap .detail .leftbox {
    width: 100%; /* 모바일에서는 100% 너비 */
    max-width: 600px; /* 너무 커지지 않도록 최대 너비 설정 */
}
.DetailWrap .detail .rightbox { 
    width: 100%; /* 모바일에서는 100% 너비 */
    max-width: 500px; /* 너무 커지지 않도록 최대 너비 설정 */
    height: auto;
}
.DetailWrap .detail .rightbox ul {}
.DetailWrap .detail .rightbox ul li {
    padding-top: 1.25rem; /* 20px 대신 rem */
}
.DetailWrap .detail .rightbox ul li .explain {font-size: 1.2rem; font-weight: 700;} /* rem 사용 */
.DetailWrap .detail .rightbox ul li h3 {font-size: 1.2rem; font-weight: 700; color: #000; padding-top: 1.25rem; display: flex; flex-direction: column;} /* rem 사용 */
.DetailWrap .detail .rightbox ul li p {font-size: 1rem; font-weight: 400; padding-top: 1rem; color: #2b2b2b;} /* rem 사용 */
.DetailWrap .detail .rightbox ul .link a {
    font-size: 1rem; /* rem 사용 */
    font-weight: 700; 
    text-decoration: underline; 
    text-underline-offset: 3px;
}


/* --- 미디어 쿼리 시작 (태블릿: 768px 이상) --- */
@media (min-width: 768px) {
    .SubWrap {
        padding-top: 80px; /* 헤더 높이 원래대로 */
    }
    .SubWrap .sub .item .listHeader {
        padding: 0 32px; /* 좌우 패딩 원래대로 */
        padding-top: 80px; /* 상단 패딩 원래대로 */
    }
    .SubWrap .sub .item .listHeader h4 {
        font-size: 1.8em; /* 원래 크기로 복귀 */
        /* width: calc(33.3333333333% - 0.6666666667 * 0.5em); -> 이 복잡한 calc 대신 flex/grid로 제어 */
        width: auto; /* 유동적으로 */
    }
    .SubWrap .sub .item .listHeader ul {
        gap: 0.5em; /* 원래 간격으로 복귀 */
        justify-content: flex-start; /* 왼쪽 정렬 */
    }
    .SubWrap .sub .item .listHeader ul li {
        width: calc(50% - 0.25em); /* 태블릿에서는 2열로 설정 */
        max-width: none; /* 최대 너비 제한 해제 */
        padding-bottom: 20px;
    }
    .SubWrap .sub .item03 .listHeader ul li {
        width: calc(50% - 0.25em); /* 태블릿에서는 2열로 설정 */
        height: auto;
    }

    /* Project Detail Page */
    .DetailWrap {
        padding-top: 80px; /* 헤더 높이 원래대로 */
    }
    .DetailWrap .intro {
        padding: 0 32px; /* 좌우 패딩 원래대로 */
        padding-top: 80px; /* 상단 패딩 원래대로 */
        gap: 0.8rem;
    }
    .DetailWrap .intro div {font-size: 1.5rem;} /* 태블릿 폰트 사이즈 조정 */
    .DetailWrap .intro h2 {font-size: 2.5rem;} /* 태블릿 폰트 사이즈 조정 */

    .DetailWrap .detail {
        padding: 0 32px; /* 좌우 패딩 원래대로 */
        padding-top: 80px; /* 상단 패딩 원래대로 */
        flex-direction: row; /* 태블릿에서는 가로 정렬 */
        justify-content: space-around; /* 요소들 간 간격 분배 */
        align-items: flex-start; /* 상단 정렬 */
    }
    .DetailWrap .detail .leftbox {
        width: 60%; /* 태블릿에서는 60% 너비 */
        max-width: 600px;
    }
    .DetailWrap .detail .rightbox { 
        width: 35%; /* 태블릿에서는 35% 너비 */
        max-width: 500px;
    }
    .DetailWrap .detail .rightbox ul li .explain {font-size: 1.3rem;}
    .DetailWrap .detail .rightbox ul li h3 {font-size: 1.3rem;}
    .DetailWrap .detail .rightbox ul li p {font-size: 1.1rem;}
    .DetailWrap .detail .rightbox ul .link a {font-size: 1.1rem;}
}

/* --- 미디어 쿼리 시작 (데스크톱: 1024px 이상) --- */
@media (min-width: 1024px) {
    /* Project List Page */
    .SubWrap .sub .item .listHeader h4 {
        /* calc()를 사용하려면 원래 의도대로 */
        width: calc(33.3333333333% - 0.6666666667 * 0.5em); 
    }
    .SubWrap .sub .item .listHeader ul li {
        width: calc(33.3333333333% - 0.6666666667 * 0.5em); /* 데스크톱에서는 3열로 설정 */
    }
    .SubWrap .sub .item03 .listHeader ul li {
        width: calc(33.3333333333% - 0.6666666667 * 0.5em); /* 데스크톱에서는 3열로 설정 */
        height: 420px; /* 데스크톱에서 원하는 고정 높이로 복귀 (이미지 비율에 따라 조절) */
    }

    /* Project Detail Page */
    .DetailWrap .intro div {font-size: 24px;} /* 원래 폰트 사이즈 */
    .DetailWrap .intro h2 {font-size: 40px;} /* 원래 폰트 사이즈 */

    .DetailWrap .detail .leftbox {
        width: 600px; /* 데스크톱에서 고정 너비로 복귀 */
    }
    .DetailWrap .detail .rightbox { 
        width: 500px; /* 데스크톱에서 고정 너비로 복귀 */
    }
    .DetailWrap .detail .rightbox ul li .explain {font-size: 24px;}
    .DetailWrap .detail .rightbox ul li h3 {font-size: 24px;}
    .DetailWrap .detail .rightbox ul li p {font-size: 20px;}
    .DetailWrap .detail .rightbox ul .link a {font-size: 20px;}
}